#hambmenu {
	position: relative;
	width: 100%;
	height: 100%;
	border: 2px #00000000 solid;
	float: left;
	margin: 10px 10px;
	transition: 0.5s ease-in-out;
	z-index: 1000;
}

#hambmenu:hover {
	border-left: 2px #000 solid;
	border-right: 2px #000 solid;
}
#hambmenu span {
	position: absolute;
	height: 2px;
	width: 100%;
	vertical-align: middle;
	background: #555;
	border-radius: 8px;
	opacity: 1;
	left: 0;
	-webkit-transform: rotate(0deg);
	transform: rotate(0deg);
	transition: 0.5s ease-in-out;
}

#hambmenu span:nth-child(1) {
	top: 0px;
	transition: all .1s ease-in;
}
#hambmenu span:nth-child(2),
#hambmenu span:nth-child(3) {
	top: 10px;
	transition: all .2s ease-in;
}
#hambmenu span:nth-child(4) {
	top: 20px;
	transition: all .3s ease-in;
}
#hambmenu:hover span {
	background: #000;
}
#hambmenu.show span {
	background: #ddd;
}
#hambmenu.show span:nth-child(1) {
	top: 18px;
	width: 0%;
	left: 50%;
	opacity: 0;
}
#hambmenu.show span:nth-child(2) {
	left: 2px;
	top: 2px;
	width: 25px;
	transition: .3s cubic-bezier(.8, .5, .2, 1.4);
	-webkit-transform: rotate(135deg);
	        transform: rotate(135deg);
	transition-delay: 100ms;
}
#hambmenu.show span:nth-child(3) {
	left: 2px;
	top: 19px;
	width: 25px;
	transition: .3s cubic-bezier(.8, .5, .2, 1.4);
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	transition-delay: 50ms;
}
#hambmenu.show span:nth-child(4) {
	left: 6px;
	top: 10px;
	width: 40px;
	transition: .3s cubic-bezier(.8, .5, .2, 1.4);
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
	transition-delay: 150ms;
}

